<template>
  <div class="hot">
    <h1 class="title-name"><slot name="title-name"></slot></h1>
    <div class="tabs">
      <el-tabs v-model="activeName" class="demo-tabs" @tab-click="handleClick">
        <el-tab-pane
          :label="item.name"
          :name="index"
          v-for="(item, index) in hot"
          :key="item.id"
        >
          <hot-list-item
            v-for="(item, index) in playListHot"
            :key="index"
            :hotList="item"
          ></hot-list-item>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
import { ref } from "@vue/reactivity";
import { useStore } from "vuex";

import hotListItem from "../../../components/hotListItem";
export default {
  name: "HotList",
  props: {
    hot: {
      type: Object,
      default() {
        return {};
      },
    },
    playListHot: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  components: {
    hotListItem,
  },
  setup(props) {
    const store = useStore();
    const activeName = ref("0");

    const handleClick = (tab) => {
      store.dispatch("getPlayListHot", tab.props.label);
    };

    return {
      handleClick,
      activeName,
    };
  },
};
</script>
<style lang="scss" scoped>
.hot {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  padding: 10px;
  border-radius: 10px;
  background: linear-gradient(145deg, #f6f6f6, #cfcfcf);
  box-shadow: 6px 6px 13px #bdbdbd, -6px -6px 13px #ffffff;
  .title-name {
    text-align: left;
    margin-right: 20px;
    width: 365px;
  }
}
.el-tab-pane {
  display: flex;
}
</style>
